/** @format */
import io from "socket.io-client";
import { appName, baseUrls, environment, apiClient } from "config";
import { store } from "state/store";

const SocketsSingleton = (function () {
    var instance;

    function createInstance() {
    	const { auth } = store.getState();
		let socket_url = baseUrls[environment].endpoints.default;
		let options = {
			secure: false,
			agent: appName+' fontend',
			'transportOptions': {
				polling: {
					extraHeaders: {
						"x-client-id": apiClient.client_id,
						"x-client-secret": apiClient.client_secret,
						//"authorization": auth.isAuthenticated? auth.token.access_token : '_',
						'Access-Control-Allow-Credentials': 'omit'
					}
				}
			},
			'rememberUpgrade': true,
			'rejectUnauthorized': false,
			'reconnection': true,
          	'reconnectionDelay': 500,
			'reconnectionAttempts': Infinity, 
			'transports': ['polling', 'websocket'],
			'extraHeaders': {
						"x-client-id": apiClient.client_id,
						"x-client-secret": apiClient.client_secret,
						//"authorization": auth.isAuthenticated? auth.token.access_token : '_',
			}
		};

		if (environment === "production") {
			options.secure = true;
		}
		// make sure socket is 
		var socket = io(socket_url, options);
		socket.on('reconnect_attempt', () => {
			////console.log("reconnect_attempt called");
			socket.io.opts.transportOptions = {
				polling: {
					extraHeaders: {
						"x-client-id": apiClient.client_id,
						"x-client-secret": apiClient.client_secret,
						//"authorization": auth.isAuthenticated? auth.token.access_token : '_',
					}
				}
			};
			socket.io.opts.extraHeaders = {
				"x-client-id": apiClient.client_id,
						"x-client-secret": apiClient.client_secret,
						//"authorization": auth.isAuthenticated? auth.token.access_token : '_',
			};
		});

        return socket;
    }
 
    return {
        getInstance: function () {
            if (!instance) {
                instance = createInstance();
            }
            return instance;
        }
    };
})();
export const socket = SocketsSingleton.getInstance();
